
import { defineComponent, PropType } from "vue";

// big circular link button with text and sub text
export default defineComponent({
  props: {
    // where to link to
    to: String,
    // icon to show in button
    icon: {
      type: String,
      required: true,
    },
    // main text
    title: {
      type: String,
      required: true,
    },
    // secondary text
    subtitle: String,
    // visual design
    design: {
      default: "big",
      type: String as PropType<"small" | "big">,
    },
  },
});
