
import { defineComponent } from "vue";

// block quote specifically for a citation
export default defineComponent({
  props: {
    // work title
    title: String,
    // list of authors
    authors: String,
    // journal, issue, date, or other misc info
    details: Array,
  },
  computed: {
    detailsString() {
      return (this.details || []).filter((e) => e).join("&nbsp;  &nbsp;");
    },
  },
});
